import axios from 'axios'

const apiClient = axios.create({
  baseURL: `https://grupo19.proyecto2019.linti.unlp.edu.ar/`,
  withCredentials: false, // This is the default
})

export default {
  getEscuelas() {return apiClient.get("apiescuelas")},
  getGeneros() {return apiClient.get("apigeneros")},
  getOauth() { return apiClient.get("oauthloged") },
  getBarrios() {return apiClient.get("apibarrios")},
  getNiveles() {return apiClient.get("apiniveles")},
  getNucleos() {return apiClient.get("apinucleos")},
  getTalleresN() {return apiClient.get("apitalleres")},
  getInfo() {return apiClient.get("info")},
  loadInfo(configuracion) {
    const fd = new FormData();
    fd.append('titulo', configuracion.titulo);
    fd.append('descripcion', configuracion.descripcion);
    fd.append('contacto', configuracion.contacto);
    fd.append('cantidadfilas', configuracion.cantidadfilas);
    if (configuracion.habilitado) {
      fd.append('habilitado', 1);
    } else {
      fd.append('habilitado', 0);
    }
    return apiClient.put("/info", fd,  {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  login(email, password) {
    const fd = new FormData();
    fd.append('email', email);
    fd.append('password', password);
    return apiClient.post("login", fd);
  },
  logout() {
    return apiClient.post("logout")
  },
  toggleUsuario(id, option) {
    const fd = new FormData();
    return apiClient.put("/user/"+id+"/"+option, fd, {headers: {"Authorization": "Bearer "+localStorage.token}})},
  getUsuarioToken() {return apiClient.get("getWithToken", {headers: {"Authorization": "Bearer "+localStorage.token}})},
  getUsuarioToken2(token) {return apiClient.get("getWithToken", {headers: {"Authorization": "Bearer "+token}})},
  getUsuario(email) {return apiClient.get("user/"+email, {headers: {"Authorization": "Bearer "+localStorage.token}})},
  getUsuarios() {return apiClient.get("user", {headers: {"Authorization": "Bearer "+localStorage.token}})},
  createUsuario(usuario) {
    const fd = new FormData();
    fd.append('fullname', usuario.firstname);
    fd.append('lastname', usuario.lastname);
    fd.append('email', usuario.email);
    fd.append('password', usuario.email);
    return apiClient.post("/user", fd,  {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  modificarUsuario(usuario) {
    const fd = new FormData();
    fd.append('id', usuario.id);
    fd.append('fullname', usuario.firstname);
    fd.append('lastname', usuario.lastname);
    fd.append('email', usuario.email);
    if (usuario.inadministrador) {fd.append('inadministrador', 1) } else {fd.append('inadministrador', 0)}
    if (usuario.inpreceptor) {fd.append('inpreceptor', 1) } else {fd.append('inpreceptor', 0)}
    fd.append('emailPrevio', usuario.emailPrev);
    return apiClient.put("/user", fd,  {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  cambiarContra(contraseña, email) {
    const fd = new FormData();
    fd.append('password', contraseña);
    return apiClient.put("/user/"+email, fd,  {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  deleteUsuario(idU) {
    return apiClient.delete("/user/"+idU, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },

  getDocente(email) {return apiClient.get("docentes/"+email, {headers: {"Authorization": "Bearer "+localStorage.token}})},
  getDocentes() {return apiClient.get("docentes", {headers: {"Authorization": "Bearer "+localStorage.token}})},
  createDocente(docente) {
    const fd = new FormData();
    fd.append('nombre', docente.nombre);
    fd.append('apellido', docente.apellido);
    fd.append('email', docente.email);
    fd.append('fecha', docente.fecha_nac);
    fd.append('doc', docente.numero);
    fd.append('telefono', docente.tel);
    fd.append('tipodoc', docente.tipo_doc_id);
    fd.append('domicilio', docente.domicilio);
    fd.append('localidad', docente.localidad_id);
    fd.append('password', docente.numero);
    return apiClient.post("/docentes", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  modificarDocente(docente) {
    const fd = new FormData();
    fd.append('id', docente.id);
    fd.append('nombre', docente.nombre);
    fd.append('apellido', docente.apellido);
    fd.append('email', docente.email);
    fd.append('fecha', docente.fecha_nac);
    fd.append('doc', docente.numero);
    fd.append('telefono', docente.tel);
    if (docente.tipo_doc_id.id) {fd.append('tipodoc', docente.tipo_doc_id.id) } else {fd.append('tipodoc', docente.tipo_doc_id)}
    fd.append('domicilio', docente.domicilio);
    if (docente.localidad_id.id) {fd.append('localidad', docente.localidad_id.id) } else {fd.append('localidad', docente.localidad_id)}
    fd.append('emailPrevio', docente.email);
    fd.append('password', docente.password);
    return apiClient.put("/docentes", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  deleteDocente(idDoc) {
    return apiClient.delete("/docentes/"+idDoc, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  getEstudiantes() {
    return apiClient.get("estudiante", {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  createEstudiante(estudiante) {
    const fd = new FormData();
    fd.append('nombre', estudiante.nombre);
    fd.append('apellido', estudiante.apellido);
    fd.append('fecha_nac', estudiante.fecha_nac);
    fd.append('numero_doc', estudiante.numero);
    fd.append('numero', estudiante.tel);
    if (estudiante.tipo_doc_id.id) {fd.append('tipodoc', estudiante.tipo_doc_id.id) } else {fd.append('tipodoc', estudiante.tipo_doc_id)}
    fd.append('domicilio', estudiante.domicilio);
    if (estudiante.localidad_id.id) {fd.append('localidad', estudiante.localidad_id.id) } else {fd.append('localidad', estudiante.localidad_id)}
    if (estudiante.barrio_id.id) {fd.append('barrio', estudiante.barrio_id.id) } else {fd.append('barrio', estudiante.barrio_id)}
    if (estudiante.genero_id.id) {fd.append('genero', estudiante.genero_id.id) } else {fd.append('genero', estudiante.genero_id)}
    if (estudiante.nivel_id.id) {fd.append('nivel', estudiante.nivel_id.id) } else {fd.append('nivel', estudiante.nivel_id)}
    if (estudiante.escuela_id.id) {fd.append('escuela', estudiante.escuela_id.id) } else {fd.append('escuela', estudiante.escuela_id)}
    fd.append('lugar_nac', estudiante.lugar_nac);
    if (estudiante.responsable.id) {fd.append('responsable', estudiante.responsable.id) } else {fd.append('responsable', estudiante.responsable)}
    return apiClient.post("/estudiante", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  modificarEstudiante(estudiante) {
    const fd = new FormData();
    fd.append('id', estudiante.id);
    fd.append('nombre', estudiante.nombre);
    fd.append('apellido', estudiante.apellido);
    fd.append('fecha_nac', estudiante.fecha_nac);
    fd.append('numero_doc', estudiante.numero);
    fd.append('numero', estudiante.tel);
    if (estudiante.tipo_doc_id.id) {fd.append('tipodoc', estudiante.tipo_doc_id.id) } else {fd.append('tipodoc', estudiante.tipo_doc_id)}
    fd.append('domicilio', estudiante.domicilio);
    if (estudiante.localidad_id.id) {fd.append('localidad', estudiante.localidad_id.id) } else {fd.append('localidad', estudiante.localidad_id)}
    if (estudiante.barrio_id.id) {fd.append('barrio', estudiante.barrio_id.id) } else {fd.append('barrio', estudiante.barrio_id)}
    if (estudiante.genero_id.id) {fd.append('genero', estudiante.genero_id.id) } else {fd.append('genero', estudiante.genero_id)}
    if (estudiante.nivel_id.id) {fd.append('nivel', estudiante.nivel_id.id) } else {fd.append('nivel', estudiante.nivel_id)}
    if (estudiante.escuela_id.id) {fd.append('escuela', estudiante.escuela_id.id) } else {fd.append('escuela', estudiante.escuela_id)}
    fd.append('lugar_nac', estudiante.lugar_nac);
    if (estudiante.responsable.id) {fd.append('responsable', estudiante.responsable.id) } else {fd.append('responsable', estudiante.responsable)}
    return apiClient.put("/estudiante", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  deleteEstudiante(idEst) {
    return apiClient.delete("/estudiante/"+idEst, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },

  getInstrumentos() {
    return apiClient.get("instrumentos", {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  createInstrumento(instrumento) {
    const fd = new FormData();
    fd.append('nombre', instrumento.nombre);
    if (instrumento.tipo_id.id) {fd.append('tipo', instrumento.tipo_id.id) } else {fd.append('tipo', instrumento.tipo_id)}
    fd.append('numeroInventario', instrumento.n_inventario);
    fd.append('latitud', instrumento.latitud);
    fd.append('longitud', instrumento.longitud);
    if (instrumento.id_alumno.id) {fd.append('alumnoInstrumento', instrumento.id_alumno.id) } else {fd.append('alumnoInstrumento', instrumento.id_alumno)}
    return apiClient.post("/instrumentos", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  modificarInstrumento(instrumento) {
    const fd = new FormData();
    fd.append('instrumento_id', instrumento.id);
    fd.append('nombre', instrumento.nombre);
    if (instrumento.tipo_id.id) {fd.append('tipo', instrumento.tipo_id.id) } else {fd.append('tipo', instrumento.tipo_id)}
    fd.append('numeroInventario', instrumento.n_inventario);
    fd.append('latitud', instrumento.latitud);
    fd.append('longitud', instrumento.longitud);
    if (instrumento.id_alumno.id) {fd.append('alumnoInstrumento', instrumento.id_alumno.id) } else {fd.append('alumnoInstrumento', instrumento.id_alumno)}
    return apiClient.put("/instrumentos", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  deleteInstrumento(idI) {
    return apiClient.delete("/instrumentos/"+idI, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },

  uploadFile(foto, num){
    const fd = new FormData();
    fd.append('file', foto, 'file');
    fd.append('n_inventario', num);
    return apiClient.post("/cargarArchivo", fd)
  },

  getCiclos() {
    return apiClient.get("ciclo_lectivo", {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  createCiclo(ciclo) {
    const fd = new FormData();
    fd.append('año', ciclo.year);
    fd.append('numeroSemestre', ciclo.semestre);
    fd.append('inicioSemestre', ciclo.fecha_ini);
    fd.append('finSemestre', ciclo.fecha_fin);
    return apiClient.post("/ciclo_lectivo", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  
  getTaller(ciclo, taller) {
    return apiClient.get("/taller/"+ciclo+"/"+taller, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  createTaller(taller) {
    const fd = new FormData();
    fd.append('añoTaller', taller.year);
    fd.append('semestreTaller', taller.semestre);
    fd.append('tallerId', taller.taller_id);
    return apiClient.post("/taller", fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  addDocenteTaller(ciclo, taller, docente) {
    const fd = new FormData();
    return apiClient.post("/tallerD/"+ciclo+"/"+taller+"/"+docente, fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  addEstudianteTaller(ciclo, taller, estudiante) {
    const fd = new FormData();
    return apiClient.post("/tallerE/"+ciclo+"/"+taller+"/"+estudiante, fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  deleteDocenteTaller(ciclo, taller, docente) {
    return apiClient.delete("/tallerD/"+ciclo+"/"+taller+"/"+docente,  {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  deleteEstudianteTaller(ciclo, taller, estudiante) {
    return apiClient.delete("/tallerE/"+ciclo+"/"+taller+"/"+estudiante, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },

  getHorarios(ciclo, taller, docente) {
    return apiClient.get("/horario/"+ciclo+"/"+taller+"/"+docente, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  agregarHorario(horario) {
    const fd = new FormData();
    fd.append('id_drt', horario.id_drt);
    fd.append('nucleo', horario.nucleo);
    fd.append('dia', horario.dia);
    fd.append('inicio', horario.inicio);
    fd.append('fin', horario.fin);
    return apiClient.post("/horario/"+1+"/"+1+"/"+1 , fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  getAsistencias(ciclo, taller, fecha) {
    return apiClient.get("/asistencia/"+ciclo+"/"+taller+"/"+fecha, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
  pasarAsistencia(asistencia, fecha, drt) {
    const fd = new FormData();
    fd.append('id_drt', drt);
    fd.append('fecha', fecha);
    fd.append('estado', asistencia.estado);
    fd.append('estudianteId', asistencia.id);
    return apiClient.post("/asistencia/"+1+"/"+1+"/"+fecha , fd, {headers: {"Authorization": "Bearer "+localStorage.token}})
  },
}