<template>
  <v-app class="black">
    <v-progress-linear
      :active="$store.state.loading"
      :indeterminate="$store.state.loading"
      absolute
      top
      color="orange accent-4"
    ></v-progress-linear>
    <router-view />
    <v-snackbar v-model="$store.state.snackBar" :timeout=4000 bottom>
      {{ $store.state.snackText }}
    </v-snackbar>
  </v-app>
</template>

<script>
import apiOrquesta from "@/services/apiOrquesta.js";
export default {
  data() {
    return {
      validado: false,
    };
  },
  async created() {
    await this.checkOauth();
    if (this.validado) {
      if (localStorage.token) {
        if (!this.$store.state.user.email) {
          await apiOrquesta.getUsuarioToken().then((response) => {
            this.$store.commit("setUser", response.data);
          });
        }
        this.validado = false;
        this.$router.push({ name: "Dash" });
      }
    }
  },
  updated() {
    if (
      this.$router.currentRoute.name != "Home" &&
      this.$router.currentRoute.name != "Login"
    ) {
      this.checkSession();
    }
  },
  methods: {
    async checkOauth() {
      await apiOrquesta.getOauth().then((response) => {
        if (response.data.token != null){
          this.$store.dispatch("login", response.data.token);
          this.validado = true;
        }
      }).catch(err => console.log("Ocurrio un error: ", err));
    },
    async checkSession() {
      if (localStorage.token) {
        if (!this.$store.state.user.email) {
          await apiOrquesta.getUsuarioToken().then((response) => {
            this.$store.commit("setUser", response.data);
          });
        }
      } else {
        alert("Debe iniciar sesion");
        this.$router.push({ name: "Home" });
      }
    },
  },
};
</script>
