import Vue from 'vue'
import Vuex from 'vuex'
import apiOrquesta from '@/services/apiOrquesta.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading: false,
    snackBar: false,
    snackText: "",
    email:'',
    user: { 
      email:'',
      nombre: '',
      apellido: '',
      roles: [],
      permisos: [],
    } 
  },
  mutations: {
    setEmail(state, email) {
      state.email = email;
    },
    setUser(state, user) {
      state.user = user;
    },
    activeSnack: (state, text) =>
      {
        state.snackBar = true,
        state.snackText = text
      },
    activateLoading: (state) =>
      {state.loading = true},
    deactivateLoading: (state) =>
      {state.loading = false},
  },
  actions: {
    login ({ commit }, token) {
      localStorage.setItem('token', token)
      return apiOrquesta.getUsuarioToken2(token)
      .then(({ data }) => {
        commit('setUser', data)
      })
    }
  },
  modules: {
  }
})
